import { Experience } from './types';

export const compareExperiences = (a: Experience, b: Experience) => {
  const aStart = a.dates.start.year * 12 + (a.dates.start.month || 0);
  const bStart = b.dates.start.year * 12 + (b.dates.start.month || 0);

  if (aStart === bStart) {
    const aEnd = 12 * (a.dates.end?.year || 0) + (a.dates.end?.month || 0);
    const bEnd = 12 * (b.dates.end?.year || 0) + (b.dates.end?.month || 0);

    return aEnd - bEnd;
  }

  return aStart - bStart;
};
