import { selector } from 'recoil';
import { companiesState, experiencesState, skillsState } from './atoms';
import { Job } from './types';
import { compareExperiences } from './utils';
import * as _ from 'lodash';

export const jobsSelector = selector({
  key: 'jobsSelector',
  get: ({ get }) => {
    const experiences = get(experiencesState);

    const jobs: Job[] = [];
    let currentJob: Job | undefined = undefined;
    experiences
      .sort(compareExperiences)
      .reverse()
      .forEach((experience) => {
        if (currentJob === undefined || currentJob.companyId !== experience.companyId) {
          currentJob = {
            companyId: experience.companyId,
            dates: {
              start: experience.dates.start,
              end: experience.dates.end,
            },
            experiences: [],
          };

          jobs.push(currentJob);
        }
        if (currentJob !== undefined) {
          currentJob.dates.start = experience.dates.start;
          currentJob.experiences.push(experience);
        }
      });

    return jobs;
  },
});

export const companiesByIdSelector = selector({
  key: 'companiesByIdSelector',
  get: ({ get }) => {
    const companies = get(companiesState);
    return _.keyBy(companies, 'id');
  },
});

export const skillsByIdSelector = selector({
  key: 'skillsByIdSelector',
  get: ({ get }) => {
    const skills = get(skillsState);
    return _.keyBy(skills, 'id');
  },
});
