import * as React from 'react';
import styled from 'styled-components';

const Root = styled.div`
  width: 100%;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  background: #eaeaea;
  padding: 10px 0;
`;

const Person = styled.div`
  font-size: 30px;
  grid-column-start: 2;
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
`;

const PhotoShadow = styled.div`
  border-radius: 75px;
  width: 100px;
  height: 100px;
  box-shadow: 0px 4px 2px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 2px 6px 0px rgb(0 0 0 / 12%);
`;

const PhotoFrame = styled.div`
  border: 4px solid #555;
  border-radius: 75px;
  border-image: linear-gradient(to bottom right, #888, #222) 1 fill;
  clip-path: circle(50px);
  width: 100px;
  height: 100px;
  box-shadow: 0px 4px 2px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 2px 6px 0px rgb(0 0 0 / 12%);
`;

const Photo = styled.img``;

const Name = styled.div``;

const Tagline = styled.div`
  color: #666;
  font-size: 0.5em;
`;

export const ResumeHeader = () => {
  return (
    <Root>
      <Person>
        <PhotoShadow>
          <PhotoFrame>
            <Photo src="/data/images/geoff_100x100.png" />
          </PhotoFrame>
        </PhotoShadow>
        <Name>Geoff Cox</Name>
        <Tagline>Talented Developer | Thoughtful Leader | Kind Person</Tagline>
      </Person>
    </Root>
  );
};
