import * as React from 'react';
import styled from 'styled-components';

const Root = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  padding: 4px;
`;

const PivotItem = styled.div`
  display: flex;
  flex-direction: column;
  align-items: stretch;
  margin: 0 8px 0 0;
  min-width: 75px;
`;

const PivotButton = styled.button.attrs(
  ({ color, selectedColor, selected }: { color: string; selectedColor: string; selected: boolean }): any => ({
    color: selected ? selectedColor : color,
    hoverColor: selectedColor,
  })
)`
  font-size: 30px;
  padding: 4px 4px 8px 4px;
  border: none;
  border-bottom: 1px solid transparent;
  color: ${(props) => props.color};
  transition: color 0.5s;
  user-select: none;
  outline: none;
  &:hover {
    color: ${(props) => props.hoverColor};
  }
`;

const PivotIndicator = styled.div`
  position: relative;
  height: 14px;
  margin: -4px 0 0 0;
`;

const PivotUnderline = styled.div.attrs(({ color }: { color: string }): any => ({
  color: color,
}))`
  position: absolute;
  left: 0;
  top: 4px;
  right: 0;
  bottom: 0;
  height: 2px;
  border-top-width: 2px;
  border-top-style: solid;
  border-top-color: ${(props) => props.color};
  width: 100%;
`;

export type PivotItem = {
  id?: string;
  name: string;
  color?: string;
};

type Props = {
  items: PivotItem[];
  selectedIndex?: 0;
  onSelected?: (pivot: PivotItem, index: number) => void;
  defaultItemColor?: string;
};

export const ResumePivot = (props: Props) => {
  const { items, selectedIndex: controlledSelectedIndex, onSelected, defaultItemColor = '#222' } = props;

  const [selectedIndex, setSelectedIndex] = React.useState(controlledSelectedIndex || 0);

  React.useEffect(() => {
    setSelectedIndex(controlledSelectedIndex || 0);
  }, [controlledSelectedIndex]);

  const onClick = (pivot: PivotItem, index: number) => {
    setSelectedIndex(index);
    if (onSelected) {
      onSelected(pivot, index);
    }
  };

  return (
    <Root>
      {items.map((item, index) => {
        const selected = index === selectedIndex;
        const color = item.color || defaultItemColor;
        return (
          <PivotItem key={item.id || index}>
            <PivotButton
              color={defaultItemColor}
              selectedColor={color}
              selected={selected}
              onClick={() => onClick(item, index)}
            >
              {item.name}
            </PivotButton>
            <PivotIndicator>{selected && <PivotUnderline color={color} />}</PivotIndicator>
          </PivotItem>
        );
      })}
    </Root>
  );
};
