import { atom } from 'recoil';
import { companies, experiences, skills, skillGroups } from './data';
import { Company, Experience, Skill, SkillGroup } from './types';

export const companiesState = atom<Company[]>({
  key: 'companiesState',
  default: companies,
});

export const experiencesState = atom<Experience[]>({
  key: 'experienceState',
  default: experiences,
});

export const skillsState = atom<Skill[]>({
  key: 'skillsState',
  default: skills,
});

export const skillGroupsState = atom<SkillGroup[]>({
  key: 'skillGroupsState',
  default: skillGroups,
});
