import * as React from 'react';
import styled, { css } from 'styled-components';
import { useRecoilValue } from 'recoil';
import { companiesByIdSelector, jobsSelector } from '../model/selectors';
import axios from 'axios';
import { ExperienceCard } from './ExperienceCard';
import { Experience, Job } from '../model/types';

const fullDivCss = css`
  width: 100%;
  height: 100%;
  overflow: hidden;
`;

const Root = styled.div`
  ${fullDivCss}
  background: #efefef;
  color: #222;
  display: grid;
  grid-template-columns: 500px 1fr;
  grid-template-rows: 1fr;
`;

const Nav = styled.div`
  ${fullDivCss}
  overflow: auto;
  padding: 5px;
  user-select: none;
`;

const Timeline = styled.div`
  width: 100%;
  display: grid;
  grid-template-columns: auto 1fr;
`;

const TransitionDate = styled.div`
  font-size: 12px;
  padding-right: 5px;
  align-self: center;
  align-content: center;
  justify-self: flex-end;
  min-height: 12px;
  vertical-align: center;
  text-transform: uppercase;
`;

const Transition = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 0;
`;

const TransitionDivider = styled.div`
  background: steelblue;
  height: 1px;
  width: 100%;
`;

const Company = styled.div`
  font-size: 12px;
  padding: 5px 0;
  text-transform: uppercase;
  min-height: 20px;

  &:after {
    content: '.';
    opacity: 0;
    user-select: none;
  }
`;

const Experiences = styled.div`
  display: flex;
  flex-direction: column;
  cursor: pointer;
  align-self: center;
  grid-column-start: 2;
`;

const ExperienceItem = styled.div.attrs((props: { selected: boolean }) => ({
  selected: props.selected,
}))`
  background: ${(props) => (props.selected ? 'steelblue' : 'white')};
  transition: background-color 0.3s, color 0.3s, border-color 0.3s;
  color: ${(props) => (props.selected ? '#fefefe' : '#222')};
  border: 1px solid #222;
  border-color: ${(props) => (props.selected ? '#fefefe' : '#222')};
  border-radius: 4px;
  box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
  margin: 5px;
  padding: 25px;

  & .role {
    color: ${(props) => (props.selected ? '#CCC' : '#888')};
  }
`;

const Name = styled.div`
  font-size: 18px;
  line-height: 24px;
`;

const Role = styled.div`
  color: #888;
  transition: color 0.3s;
`;

const Details = styled.div`
  ${fullDivCss}
  padding: 25px;
`;

export const ExperienceExplorer = () => {
  const jobs = useRecoilValue(jobsSelector);
  const companiesById = useRecoilValue(companiesByIdSelector);

  const [myMarkdown, setMyMarkdown] = React.useState('');

  const [currentExperience, setCurrentExperience] = React.useState<Experience | undefined>(undefined);

  React.useEffect(() => {
    axios.get<string>('/data/detail.md').then((response) => {
      setMyMarkdown(response.data);
    });
  }, []);

  const renderJobTransition = (current?: Job, previous?: Job) => {
    const year = current?.dates.start.year || previous?.dates.end?.year || 'Present';
    const currentCompany = current && companiesById[current.companyId];
    const previousCompany = previous && companiesById[previous.companyId];
    return (
      <React.Fragment key={`${year}-${currentCompany?.name}`}>
        <TransitionDate key={`${year}`}>{year}</TransitionDate>
        <Transition>
          <Company>{currentCompany?.name}</Company>
          <TransitionDivider />
          <Company>{previousCompany?.name}</Company>
        </Transition>
      </React.Fragment>
    );
  };

  const renderExperiences = (job: Job) => {
    return (
      <Experiences key={`${job.companyId}-experiences`}>
        {job.experiences.map((experience) => (
          <ExperienceItem
            key={experience.id}
            onClick={() => setCurrentExperience(experience)}
            selected={currentExperience?.id === experience.id}
          >
            <Name>{experience.name}</Name>
            <Role className="role">{experience.role}</Role>
          </ExperienceItem>
        ))}
      </Experiences>
    );
  };

  return (
    <Root>
      <Nav>
        <Timeline>
          {jobs.map((job, index, jobsArray) => {
            const previousJob = index - 1 >= 0 ? jobsArray[index - 1] : undefined;
            return (
              <React.Fragment key={index}>
                {renderJobTransition(previousJob, job)}
                {renderExperiences(job)}
              </React.Fragment>
            );
          })}
        </Timeline>
      </Nav>
      <Details>{currentExperience && <ExperienceCard experience={currentExperience} />}</Details>
    </Root>
  );
};
