import * as React from 'react';
import { RecoilRoot } from 'recoil';
import styled, { css } from 'styled-components';
import { ExperienceExplorer } from './ExperienceExplorer';
import './stylesheet.css';
import { ResumeHeader } from './ResumeHeader';
import { ResumePivot } from './ResumePivot';
import { Router, navigate, RouteComponentProps, useLocation } from '@reach/router';

const fullDivCss = css`
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
`;

const Root = styled.div`
  ${fullDivCss}
`;

const Layout = styled.div`
  ${fullDivCss}
  display: grid;
  grid-template-rows: auto auto 1fr;
  padding-bottom: 25px;
`;

const PivotArea = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  padding-top: 25px;
`;

const pivotItems = [
  {
    id: 'pro',
    name: 'Professional Experience',
    color: '#4682b4',
  },
  {
    id: 'skills',
    name: 'Skills',
    color: '#F9BA32',
  },
  {
    id: 'origin',
    name: 'Origin Story',
    color: '#D9593D',
  },
  {
    id: 'next',
    name: 'Next Evolution',
    color: '#635C91',
  },
  {
    id: 'interview',
    name: 'Virtual Interview',
    color: '#43B759',
  },
];

export const App = () => {
  const Experiences = (_props: RouteComponentProps) => <ExperienceExplorer />;
  const Skills = (_props: RouteComponentProps) => <div>Skills</div>;
  const OriginStory = (_props: RouteComponentProps) => <div>Origin Story</div>;
  const NextEvolution = (_props: RouteComponentProps) => <div>Next Evolution</div>;
  const VirtualInterview = (_props: RouteComponentProps) => <div>Virtual Interview</div>;

  const Thing = (_props: RouteComponentProps) => {
    const location = useLocation();
    console.log(location.pathname);
    return <div />;
  };

  return (
    <Root>
      <RecoilRoot>
        <Layout>
          <ResumeHeader />
          <PivotArea>
            <ResumePivot items={pivotItems} onSelected={(pivot, _index) => navigate(`/${pivot.id}`)} />
          </PivotArea>
          <Router>
            <Thing path="/pro2" />
            <Experiences path="/pro" />
            <Skills path="/skills" />
            <OriginStory path="/origin" />
            <NextEvolution path="/next" />
            <VirtualInterview path="/interview" />
          </Router>
        </Layout>
      </RecoilRoot>
    </Root>
  );
};
