import * as React from 'react';
import styled from 'styled-components';
import { Experience } from '../model/types';
import { useRecoilValue } from 'recoil';
import { companiesByIdSelector, skillsByIdSelector } from '../model/selectors';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faIdBadge, faSitemap, faBuilding, faStar } from '@fortawesome/free-solid-svg-icons';
const Root = styled.div`
  color: #222;
  background: white;
  border: 1px solid #222;
  border-radius: 4px;
  padding: 25px;
  max-width: 900px;
  box-shadow: 0px 2px 1px -1px rgb(0 0 0 / 20%), 0px 1px 1px 0px rgb(0 0 0 / 14%), 0px 1px 3px 0px rgb(0 0 0 / 12%);
`;

const Header = styled.div``;

const Name = styled.div`
  font-size: 32px;
  color: steelblue;
  padding-bottom: 10px;
`;

const Company = styled.div``;

const Role = styled.div``;

const Team = styled.div``;

const Subtitle = styled.div`
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  font-size: 16px;
  color: #888;
`;

const SubtitleIcon = styled(FontAwesomeIcon)`
  margin: 0 5px 0 0;
  font-size: 20px;
`;

const SubtitleDivider = styled.span`
  &:after {
    content: '|';
  }
  color: steelblue;
  font-size: 20px;
  padding: 0 4px;
`;

const SectionLabel = styled.div`
  font-size: 20px;
  padding: 20px 0 0 0;
`;

const Accomplishments = styled.div`
  display: flex;
  flex-direction: column;
`;

const AccomplishmentIcon = styled(FontAwesomeIcon)`
  margin: 0 5px 0 0;
  color: steelblue;
`;

const Accomplishment = styled.div`
  font-size: 16px;
  padding: 20px 0 0 20px;
`;

const Skills = styled.div`
  display: flex;
  flex-direction: row;
`;

const SkillItem = styled.div`
  padding: 5px;
`;

const Details = styled.div`
  padding-top: 20px;
  font-size: 20px;
  max-width: 500px;
`;

const Summary = styled.div``;

const Detail = styled.div``;

type Props = {
  experience: Experience;
};

export const ExperienceCard = (props: Props) => {
  const { experience } = props;

  const companiesById = useRecoilValue(companiesByIdSelector);
  const company = companiesById[experience.companyId] || undefined;

  const skillsById = useRecoilValue(skillsByIdSelector);

  return (
    <Root>
      <Header>
        <Name>{experience.name}</Name>
        <Subtitle>
          <SubtitleIcon icon={faIdBadge} />
          <Role>{experience.role}</Role>
          <SubtitleDivider />
          <SubtitleIcon icon={faSitemap} />
          <Team>{experience.team}</Team>
          {company && (
            <>
              <SubtitleDivider />
              <SubtitleIcon icon={faBuilding} />
              <Company>{company.name}</Company>
            </>
          )}
        </Subtitle>
      </Header>
      {experience.accomplishments && (
        <>
          {experience.summary && (
            <Summary>
              {experience.summary.map((summaryLine, index) => (
                <p key={index}>{summaryLine}</p>
              ))}
            </Summary>
          )}
          <SectionLabel>Accomplishments</SectionLabel>
          <Accomplishments>
            {experience.accomplishments?.map((accomplishment) => (
              <Accomplishment>
                <AccomplishmentIcon icon={faStar} />
                {accomplishment}
              </Accomplishment>
            ))}
          </Accomplishments>
        </>
      )}
      {experience.keySkillIds && (
        <>
          <SectionLabel>Skills</SectionLabel>
          <Skills>
            {experience.keySkillIds.map((keySkillId) => {
              const skill = skillsById[keySkillId];
              return skill && <SkillItem>{skill.name}</SkillItem>;
            })}
          </Skills>
        </>
      )}
      {experience.otherSkillIds && (
        <>
          <Skills>
            {experience.otherSkillIds.map((otherSkillId) => {
              const skill = skillsById[otherSkillId];
              return skill && <SkillItem>{skill.name}</SkillItem>;
            })}
          </Skills>
        </>
      )}
      {experience.details && (
        <Details>
          {experience.details.map((detail) => (
            <Detail>{detail}</Detail>
          ))}
        </Details>
      )}
    </Root>
  );
};
